<template>
		<div class="tradingRecord qrcode">
			<div style="height:1px"></div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>把二维码发送给好友,好友可借用该金额消费</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardPay" width="88%">
				</div>
				<p>转发此页面给好友,借储值金额给他消费</p>
			</div>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardPay:'',
		    	currUrl:'',
		    	dataKey:'',
		    	urlShare:'',
		    	lendMoney:0,
		    	payTitle:'你的好友给您借款100元,请扫码进行消费!',
		    	payDesc:'扫一扫,向商家付款',
		    	payImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={},wxshareCardurl='';
				_this.busId= _this.$route.params.busId;
				_this.dataKey= _this.$route.params.dataKey;
				_this.lendMoney= _this.$route.params.money;
				_this.payTitle='你的好友给您借款'+_this.lendMoney+'元,请扫码进行消费!'
				_this.currUrl=window.location.href;
				_this.url=_this.currUrl.split('#');
				_this.urlShare=_this.url[0]+'#/shareOfferPay/'+_this.busId;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				wxshareCardurl=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.wxshareCard_POST;
				// _this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.authorizeOrLogin_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
	                		_this.urlCardPay=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.memberLentImage_GET+"?busId="+_this.busId+"&memberLentKey="+_this.dataKey;
	                		console.log('offerPay',_this.urlCardPay)
							Lib.M.shareCode(json, _this.urlShare, _this.payTitle, _this.payImg,_this.payDesc,wxshareCardurl);
	                	}
	                	
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	        
	    }
	}
</script>
<style scope>

</style>


